import React from 'react';
import './Nav.css'
import { Link} from 'react-router-dom'

function Nav() {

    const navStyle = {
        color: 'white'
    }

    return (
        <nav>
            <h3>Logo</h3>
            <ul className="nav-links">

                <Link style={navStyle} to='/'>
                    <li>Log in</li>
                </Link>

                <Link style={navStyle} to='/Cv'>
                    <li>Cv</li>
                </Link>

                
                <Link style={navStyle} to='/Test'>
                    <li>Test</li>
                </Link>
            </ul>
        </nav>
    );
}

export default Nav;
