import React, { Component } from 'react';
import './Login.css'
import { Input } from '../../components/atoms/input/Input.jsx'

class Test extends Component {
  constructor() {
    super();
    this.state = {
      username: '',
      password: '',
      error: '',
    }
  }

  handleClick = () => {
    let user = localStorage.getItem("username");
    let pass = localStorage.getItem("password");

    if(user === this.state.username &&
      pass === this.state.password){
        this.props.history.push("/Cv");
    }else{
      this.setState({
          error:'Wrong password or username'
        });
    }
   
  }


  handleUserChange = (evt) => {
    this.setState({
      username: evt.target.value,
    });
  };

  handlePasswordChange = (evt) => {
    this.setState({
      password: evt.target.value,
    });
  };

  render() {
    return (
      <div>
        <div className="Login">
          <h1 className = "Title">Welcome Back!</h1>
          <Input labelText={"Username"} onHandleChange={this.handleUserChange} placeHolder="your username..." />
          <Input labelText={"Password"} onHandleChange={this.handlePasswordChange} placeHolder="your password..." type="password" />

          <p className="error">
            {this.state.error}
          </p>
          
          <button className="btnLogin"
            onClick={this.handleClick} type="button">
            Log In
        </button>
        </div>
      </div>
    );
  }
}

export default Test;
