import React from 'react';
import './App.css';
import Nav from './views/nav/Nav'
import Cv from './views/cv/Cv'
import LoginPage from './Login';
import Login from './views/login/Login';

import { HashRouter } from 'react-router-dom';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

function App() {
  ///Check the local storage items
  let name = localStorage.getItem('username')
  let pass =  localStorage.getItem('password')
  //Store the info if the info dosent excist or is different
  if(name == null || name !== 'Jofiel') {
    localStorage.setItem('username', "Jofiel");
  }
  
  if(pass == null || pass !== 'contraseña'){
    localStorage.setItem('password', "contraseña");
  }
  
  return (
    <HashRouter>
      <div className="App">
        <h1>Test 2</h1>
        <Switch>
          <Route path="#/" exact component={Login} />
          <Route path="/Cv" component={Cv} />
          <Route path="/Test" component={LoginPage} />
        </Switch>
      </div>
    </HashRouter>
  );
}

const Home = () => (
  <div>
    <h1>Log in Page</h1>
  </div>
);

export default App;
